import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import shehui from '@/components/shehui'
import Today from '@/components/today'
import shipin from '@/components/shipin'
import hotspot from '@/components/hotspot'
import amuse from '@/components/amuse'
import one from '@/components/yi/one'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/shehui',
      component: shehui
    },
    {
    	path:'/',
    	name:'Today',
    	component: Today
    }
    ,
    {
    	path:'/shipin',
    	component: shipin
    }
    ,
    {
    	path:'/hotspot',
    	component:hotspot
    }
    ,
    {
    	path:'/amuse',
    	component:amuse,
    	children:[
        {
          path:'/yi/one',
          name:'one',
          component:one   
         }
             ]
    }
  ]
})



