<style>
	*{
		margin: 0;padding: 0;
		text-decoration: none;
		list-style-type: none;
		font-style:normal ;
	}
	.header{
		width: 100%;display: flex;-webkit-justify-content: space-between;
		background-color:#D44040 ;height: 2.25rem;line-height: 2.25rem;
		padding: 0 .75rem;
	}
	.navTop{
		display: flex;-webkit-justify-content: space-between;width: 100%;
		padding: 0 .75rem;font-size: .8rem;background: #F4F5F6;
	}
	.navTop li{
		width: 20%;text-align: center;line-height: 1.85rem;
	}
	.navTop li a{
		color: #000000;
	}
</style>
<template>
  <div id="app">
  	<div class="header">
  		<span><img src="./assets/xinxi.png"/></span>
  		<span><img src="./assets/logo.png"/></span>
  		<span><img src="./assets/susuo.png"/></span>
  	</div>
    	<ul class="navTop">
				<li><router-link to="/">推荐</router-link></li>
				<li><router-link to="/shipin">视频</router-link></li>
				<li><router-link to="/hotspot">热点</router-link></li>
				<li><router-link to="/shehui">社会</router-link></li>
				<li><router-link to="/amuse">娱乐</router-link></li>
		</ul>
    <router-view></router-view>
  </div>
</template>

<script>
//export default {
//name: 'app'
//
//}
(function(win,doc){
        win.onresize = function(){change()}
        function change(){
        var wt = document.documentElement.clientWidth/((720/20)/2);
        //根目录字体大小
       doc.documentElement.style.fontSize=wt+"px";

}
change();

})(window,document);
</script>





